/********* -section-START************/
/********* -section-END************/
*{
    font-family:roboto;
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
/*********Header nav-section-START************/
.nav-bar {
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 8px;
    background-color: #d5eeff;
    position: sticky;
    top:0;
    z-index: 1000;
}

.nav-title{
    font-size:32px;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace !important;
}
.nav-elements{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width:35%;
    padding:5px;
    margin: 0px;
}
.nav-tags{
    color:#1f1c3f;
    font-size:18px;
    margin:0px 10px;
    padding: 0px;
    box-sizing: border-box;
    border-bottom:solid 3px transparent;
    cursor: pointer;
}
.nav-tags:hover{
    transition: .5ms;
    border-bottom:solid 3px #020918;
}
.nav-tags-active{
    transition: .5ms;
    border-bottom:solid 3px #020918;
}
.nav-link{
    text-decoration: none;
    color: #fff;
}
.nav-sm-close{
    display: none;
}
.nav-sm-btn{
    display: none;
}
.nav-hr{
    display: none;
}
.nav-sm-open{
    display: none;
}
@media (min-width:426px) and (max-width:1024px){
    .nav-title{
        font-size:28px;
        font-weight: bold;
        font-family: 'Courier New', Courier, monospace !important;
    }    
    
.nav-tags{
    color: #fff;
    font-size:14px;
    margin:0px 10px;
    padding: 0px;
    box-sizing: border-box;
    border-bottom:solid 3px transparent;
    cursor: pointer;
}
}
@media (max-width:426px){
    
.nav-title{
    font-size:24px;
    font-weight: bold;
    font-family: 'Courier New', Courier, monospace !important;
}
.nav-elements{
    display: none;
    
}
.nav-sm-btn{
    display: block;
    border: none;
    padding: none;
    margin: none;
    background-color: transparent;
    width:30px;
    margin-right:5px;
    cursor: pointer;
}

.nav-hr{
    display: block;
    width:80vw;
    border: 2px solid #8b6b28;
    align-self: flex-start;

   
}

.nav-sm-open{
    display: flex;
    flex-direction: column;
    background-color:  rgba(51, 132, 213, 0.772);
    align-items: flex-end;
    transition:3s;
    padding-top:10px;
    padding-bottom:10px;
    width:100%;
}
  
.nav-tags{
    color: #fff;
    font-size:14px;
    margin:0px 10px;
    padding: 0px;
    box-sizing: border-box;
    border-bottom:solid 3px transparent;
    cursor: pointer;
}
}
/*********Header nav-section-END************/

