.details-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding:10px 10px 10px 12px;
    border-radius:5px;
    background-color: #524040;
    width:250px;
    box-shadow:0px -0px 9px 5px #282424;
    
    margin:10px 30px;
}
.details-img{
    align-self: center;
    width: 100%;
    height: 140px;
}
.details-title{
    font-size: 20px;
    color:#286bb1;
    margin:4px 0px;
    font-weight: bold;
}
.details-description{
    font-size: 16px;
    color:#fff;
    margin:10px 0px;
    font-family: roboto;

}
.details-hr{
    align-self: flex-end;
    width:80%;
    border: 2px solid #8b6b28;
    
    margin: 5px 0px;
}
.details-btn{
    background-color:rgba(51, 132, 213, 0.772);
    color: #fff;
    border: none;
    margin: none;
    border-radius:12px;
    padding: 10px 20px;
    margin: 5px 0px;
    cursor: pointer;
}
@media (max-width:675px){
    
.details-hr{
    align-self: flex-end;
    width:80%;
    border: 1px solid #8b6b28;
    
    margin: 4px 0px;
}
.details-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding:8px;
    border-radius:5px;
    background-color: #524040;
   
}

.details-img{
    width: 100%;
    height: 140px;
}

.details-title{
    font-size: 16px;
    color:rgba(51, 132, 213, 0.772);
    margin:2px 0px;
}
.details-description{
    font-size: 12px;
    color:#ede8e8;
    margin:7px 0px;
    font-family: roboto;

}

.details-btn{
    background-color:rgba(51, 132, 213, 0.772);
    color: #fff;
    border: none;
    margin: none;
    border-radius:12px;
    padding: 9px 9px;
    margin: 4px 0px;
    font-size:12px;
}
}