.bg-main{
    width:100vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.main-contetn{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding:90px 250px;
    min-height:30vh;
    background-color: #3c3D50;
    color: #fff;
    
}
.bg-home{
    background-image: url('https://www2.deloitte.com/content/dam/insights/us/articles/au22113_predictive-analytics-in-health-care/images/AU22113_banner.jpg/jcr:content/renditions/cq5dam.web.1440.660.jpeg');
    background-size: cover;
    height:60vh;
    width: 100vw;
    
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.main-h1{
    color:#fff;
    font-size: 28px;
    font-weight: 300;
}

