.home-abstract{
    padding:90px 250px;
    background-color:#1f1c3f;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    
}
.home-introduction-h1{
    color:#fff;
    font-family: roboto;
    font-size:32px;
    margin-bottom:20px;
}
.home-introduction-p{
    color: #fff;
    font-family: roboto;
    font-size:24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
