.home-more{
    background-color:#1f1c3f;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding:20px;
    align-items:center;
    width:100%;
}
.home-more-h1{
    align-self: flex-start;
    color: #fff;
    margin:20px 0px 20px 20vw ;
 
}
.home-more-cont{
    display:flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.head-cont{

    text-decoration: underline;
    color: #8b6b28;
    font-size:30px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.flowchart-img{
    width: 50%;
    margin:40px 0px 0px 10px;
    align-self: center;
    text-align: center;
    border-radius:12px;
}
.footer{
    height: 16vh;
    padding:15px 20px  0px 5px;
    background-color: black;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.footer-brand-set-1{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left:10px;

}
.footer-project{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size:25px;
}
.footer-project-p{
    font-size:14px;
    margin:10px 0px 0px 0px;
}
.footer-brand-set-2{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width:60%;

}
.footer-brand{
    margin:0px 15px;
    font-size: 20px;
    color: #cdbfbf;
    text-decoration: none;
    cursor: pointer;
}
@media (max-width:675px){
    .bg-main{
        width:100vw;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        
    }   
}