.bg-main1 {
    background-color: #f0f0f0;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
    min-height:100vh;
}

.bg-main1 h1 {
    font-size: 24px;
    margin-bottom: 20px;
}

.bg-main1 form {
    margin-bottom: 20px;
}

.bg-main1 label {
    font-weight: bold;
    margin-bottom: 5px;
}

.bg-main1 input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
}

.bg-main1 button {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.bg-main1 button:hover {
    background-color: #45a049;
}

.bg-main1 p {
    margin-top: 10px;
    font-size: 16px;
}
  .heart {
    margin: 70px 0px 70px 0px;
    font-size: 6em;
    position: relative;
  }
  
  .heartbeat {
    position: relative;
    z-index: 1;
    animation: beat 2s linear infinite;
  }
  
  .heartecho {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    animation: echo 2s linear infinite;
  }
  
  @keyframes beat {
    0% {
      transform: scale(1);
    }
    14% {
      transform: scale(0.9);
    }
    21% {
      transform: scale(1.1) skew(0.004turn)
    }
    28% {
      transform: scale(1) skew(0.008turn);
    }
    35% {
      transform: scale(1) skew(0)
    }
  }
  
  @keyframes echo {
    0% {
      opacity: 0.5;
      transform: scale(1);
    }
    14% {
      opacity: 0.4;
      transform: scale(0.8);
    }
    21% {
      opacity: 0.4;
      transform: scale(1.1);
    }
    100% {
      opacity: 0;
      transform: scale(3);
    }
  }
#prediction {
    background-color: #ffe0b2; /* Light orange */
    padding: 10px;
    border-radius: 5px;
}

#response {
    background-color: #aed581; /* Light green */
    padding: 10px;
    border-radius: 5px;
}
.blue-button {
    margin:20px 0px;
    background-color: #2196F3; /* Blue */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.blue-button:hover {
